import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import PropTypes from 'prop-types';
import './Settings.scss';
import genID from 'shortid';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faSave,
  faExpand,
  faImages,
  faFolder,
} from '@fortawesome/free-solid-svg-icons';
import SaveSetting from './SaveSetting';
import ResizeSetting from './ResizeSetting';
import ExportSetting from './ExportSetting';
import ImportSetting from './ImportSetting';

class Settings extends Component {
  static propTypes = {
    squareCount: PropTypes.number.isRequired,
    framesCount: PropTypes.number.isRequired,
    setNewSquareCount: PropTypes.func.isRequired,
    createSaveFileContents: PropTypes.func.isRequired,
    restoreProjectFromSave: PropTypes.func.isRequired,
  };

  LOCAL_STORAGE_KEY = 'local-piskel-safe';

  state = {
    activeSettingKey: null,
    settingsData: {
      resize: {
        id: genID(),
        icon: faExpand,
        component: ResizeSetting,
      },
      save: {
        id: genID(),
        icon: faSave,
        component: SaveSetting,
      },
      exportTo: {
        id: genID(),
        icon: faImages,
        component: ExportSetting,
      },
      importFrom: {
        id: genID(),
        icon: faFolder,
        component: ImportSetting,
      },
    },
    messageBoxData: null,
  };

  settingsContainer = null;

  MESSAGE_TIMEOUT = 2000;

  componentDidMount() {
    document.addEventListener('click', this.handleSettingClick);
  }

  componentWillUnmount() {
    document.removeEventListener('click', this.handleSettingClick);
  }

  handleSettingClick = ({ target }) => {
    const { activeSettingKey } = this.state;
    const settingsContainer = target.closest('.settings__container');
    if (!settingsContainer) {
      if (activeSettingKey) {
        this.muteSettings();
      }
      return;
    }
    const settingsOption = target.closest('.settings__option');
    if (settingsOption) {
      this.changeSetting(settingsOption);
    }
  };

  renderMessageBox = message => {
    this.setState({
      messageBoxData: message,
    });

    setTimeout(() => {
      this.setState({
        messageBoxData: null,
      });
    }, this.MESSAGE_TIMEOUT);
  };

  changeSetting = option => {
    const { activeSettingKey } = this.state;
    const clickedSettingKey = option.dataset.key;
    if (activeSettingKey === clickedSettingKey) {
      this.muteSettings();
      return;
    }
    this.setState({
      activeSettingKey: clickedSettingKey,
    });
  };

  muteSettings = () => {
    this.settingsContainer.classList.remove('settings__container--active');
    const listener = () => {
      this.settingsContainer.removeEventListener('transitionend', listener);
      this.setState({
        activeSettingKey: null,
      });
    };
    this.settingsContainer.addEventListener('transitionend', listener);
  };

  render() {
    const { settingsData, activeSettingKey, messageBoxData } = this.state;
    const {
      squareCount,
      setNewSquareCount,
      createSaveFileContents,
      restoreProjectFromSave,
      framesCount,
    } = this.props;

    const settingKeys = Object.keys(settingsData);
    const SettingsComponent = activeSettingKey
      ? settingsData[activeSettingKey].component
      : null;
    let messageBox = '';
    if (messageBoxData) {
      messageBox = ReactDOM.createPortal(
        <span className="settings__message-box">{messageBoxData}</span>,
        document.body,
      );
    }

    return (
      <section className="settings">
        <div
          ref={e => {
            this.settingsContainer = e;
          }}
          className={`settings__container ${
            activeSettingKey ? 'settings__container--active' : ''
          }`}
        >
          <div role="presentation" className="settings__list">
            {settingKeys.map(key => {
              const { id, icon } = settingsData[key];
              const isActive = key === activeSettingKey;
              return (
                <div
                  key={id}
                  className={`settings__option ${
                    isActive ? 'settings__option--active' : ''
                  }`}
                  data-key={key}
                >
                  <FontAwesomeIcon icon={icon} />
                </div>
              );
            })}
          </div>
          {SettingsComponent ? (
            <SettingsComponent
              squareCount={squareCount}
              framesCount={framesCount}
              setNewSquareCount={setNewSquareCount}
              muteSettings={this.muteSettings}
              createSaveFileContents={createSaveFileContents}
              localStorageKey={this.LOCAL_STORAGE_KEY}
              renderMessageBox={this.renderMessageBox}
              restoreProjectFromSave={restoreProjectFromSave}
            />
          ) : (
            ''
          )}
          {messageBox}
        </div>
      </section>
    );
  }
}

export default Settings;
