import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';

class SaveAsPNG extends PureComponent {
  static propTypes = {
    framesCount: PropTypes.number.isRequired,
    exportResolution: PropTypes.number.isRequired,
  };

  state = {
    rowsCount: 1,
    columnsCount: 1,
  };

  spriteSheetButton = null;

  saveAsURLButton = null;

  saveActiveFrameButton = null;

  columnsInput = null;

  handleColumnsChange = () => {
    if (this.columnsInput.value === '') {
      this.setState({
        columnsCount: '',
        rowsCount: this.getRowsCountFrom(1),
      });
      return;
    }

    const { framesCount } = this.props;
    let newColumnsCount = Number(this.columnsInput.value);

    if (Number.isNaN(newColumnsCount)) {
      return;
    }

    if (newColumnsCount > framesCount) {
      newColumnsCount = framesCount;
    }

    if (newColumnsCount <= 0) {
      newColumnsCount = 1;
    }

    this.setState({
      columnsCount: newColumnsCount,
      rowsCount: this.getRowsCountFrom(newColumnsCount),
    });
  };

  getRowsCountFrom = val => {
    const { framesCount } = this.props;
    return Math.ceil(framesCount / val);
  };

  getColumnsFromRows = () => {
    const { columnsCount } = this.state;
    if (columnsCount === '') {
      return 1;
    }
    return Number(columnsCount);
  };

  saveActiveFrame = () => {
    const { exportResolution } = this.props;
    const canvas = document.createElement('canvas');
    canvas.width = exportResolution;
    canvas.height = exportResolution;
    // TODO: continue
  };

  saveAsSpritesheet = () => {
    const { rowsCount } = this.state;
    const columnsCount = this.getColumnsFromRows();
    for (let rows = 0; rows < rowsCount; rows += 1) {
      for (let columns = 0; columns < columnsCount; columns += 1) {
        // TODO: continue
      }
    }
  };

  onClick = ({ target }) => {
    if (target === this.spriteSheetButton) {
      this.saveAsSpritesheet();
      console.log('Save as Spritesheet');
    } else if (target === this.saveAsURLButton) {
      console.log('Save as URL');
    } else if (target === this.saveActiveFrameButton) {
      this.saveActiveFrame();
    }
  };

  render() {
    const { rowsCount, columnsCount } = this.state;

    return (
      <div
        onClick={this.onClick}
        role="presentation"
        className="export-options__main"
      >
        <div
          style={{ flexFlow: 'column nowrap', alignItems: 'flex-start' }}
          className="active-setting-block__row active-setting-block__row--framed"
        >
          <p className="active-setting-block__row">
            <span className="active-setting-block__text active-setting-block__text--highlighted">
              Spritesheet layout options:
            </span>
          </p>
          <div className="active-setting-block__row">
            <span className="active-setting-block__text">Columns:</span>
            <input
              ref={e => {
                this.columnsInput = e;
              }}
              onChange={this.handleColumnsChange}
              style={{ width: '59px', margin: '0 0.3rem' }}
              className="active-setting-block__input"
              value={columnsCount}
              type="text"
            />
            <span className="active-setting-block__text">Rows:</span>
            <input
              style={{ width: '59px', margin: '0 0.3rem' }}
              className="active-setting-block__input active-setting-block__input--disabled"
              value={rowsCount}
              type="text"
              disabled
            />
          </div>
        </div>
        <p className="active-setting-block__row">
          <span className="active-setting-block__text active-setting-block__text--italic">
            Export your animation as a PNG spritesheet containing all frames.
          </span>
        </p>
        <div
          style={{ flexFlow: 'column nowrap', alignItems: 'flex-start' }}
          className="active-setting-block__row active-setting-block__row--framed"
        >
          <p style={{ padding: 0 }} className="active-setting-block__row">
            <span className="active-setting-block__text active-setting-block__text--highlighted">
              Spritesheet file export:
            </span>
          </p>
          <p className="active-setting-block__row">
            <button
              ref={e => {
                this.spriteSheetButton = e;
              }}
              style={{ marginRight: '0.7rem', width: '80px' }}
              type="button"
              className="active-setting-block__button active-setting-block__button"
            >
              Download
            </button>
            <span style={{ flex: 1 }} className="active-setting-block__text">
              {`1024 x 1024 px, 1 frame`}
              <br />
              {`1 column, 1 row.`}
            </span>
          </p>
        </div>
        <div
          style={{ flexFlow: 'column nowrap', alignItems: 'flex-start' }}
          className="active-setting-block__row active-setting-block__row--framed"
        >
          <p style={{ padding: 0 }} className="active-setting-block__row">
            <span className="active-setting-block__text active-setting-block__text--highlighted">
              Spritesheet data-uri export:
            </span>
          </p>
          <p className="active-setting-block__row">
            <button
              ref={e => {
                this.saveAsURLButton = e;
              }}
              style={{ marginRight: '0.7rem', width: '80px' }}
              type="button"
              className="active-setting-block__button active-setting-block__button"
            >
              Open
            </button>
            <span style={{ flex: 1 }} className="active-setting-block__text">
              Open the spritesheet as a data-uri
            </span>
          </p>
        </div>
        <div
          style={{ flexFlow: 'column nowrap', alignItems: 'flex-start' }}
          className="active-setting-block__row active-setting-block__row--framed"
        >
          <p style={{ padding: 0 }} className="active-setting-block__row">
            <span className="active-setting-block__text active-setting-block__text--highlighted">
              Selected frame export:
            </span>
          </p>
          <p className="active-setting-block__row">
            <button
              ref={e => {
                this.saveActiveFrameButton = e;
              }}
              style={{ marginRight: '0.7rem', width: '80px' }}
              type="button"
              className="active-setting-block__button active-setting-block__button"
            >
              Download
            </button>
            <span style={{ flex: 1 }} className="active-setting-block__text">
              Single frame as a PNG file
            </span>
          </p>
        </div>
      </div>
    );
  }
}

export default SaveAsPNG;
