import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import Canvas from '../Canvas/Canvas';

class EyeDrop extends PureComponent {
  static propTypes = {
    getSquareFromCoords: PropTypes.func.isRequired,
    activeDraftCtx: PropTypes.shape({
      canvas: PropTypes.shape({
        width: PropTypes.number.isRequired,
        height: PropTypes.number.isRequired,
      }).isRequired,
    }),
    canvasHelpers: PropTypes.shape({
      eyeDrop: PropTypes.func.isRequired,
      colorDataToHex: PropTypes.func.isRequired,
    }).isRequired,
    paletteWidth: PropTypes.number.isRequired,
    paletteHeight: PropTypes.number.isRequired,
    imageSize: PropTypes.number.isRequired,
    squareCount: PropTypes.number.isRequired,
    paletteHelperCanvasRef: PropTypes.func.isRequired,
    onMount: PropTypes.func.isRequired,
    updateHelperCanvas: PropTypes.func.isRequired,
    primaryColor: PropTypes.string,
    setColors: PropTypes.func.isRequired,
    sharedMouseMove: PropTypes.func.isRequired,
    sharedMouseDown: PropTypes.func.isRequired,
    sharedMouseUp: PropTypes.func.isRequired,
    sharedMouseLeave: PropTypes.func.isRequired,
  };

  static defaultProps = {
    primaryColor: null,
    activeDraftCtx: null,
  };

  paletteCtx = null;

  componentDidMount() {
    this.init();
    const { onMount, updateHelperCanvas } = this.props;
    onMount(this.paletteCtx);
    updateHelperCanvas();
  }

  componentDidUpdate() {
    const { updateHelperCanvas } = this.props;
    updateHelperCanvas();
  }

  init = () => {
    this.paletteCtx = this.paletteCanvas.getContext('2d');
  };

  onMouseMove = e => {
    const { sharedMouseMove } = this.props;
    sharedMouseMove(e);
  };

  onMouseDown = () => {
    const { sharedMouseDown } = this.props;
    sharedMouseDown();
  };

  onMouseUp = e => {
    const { sharedMouseUp } = this.props;
    sharedMouseUp(e);
  };

  onMouseLeave = () => {
    const { sharedMouseLeave } = this.props;
    sharedMouseLeave();
  };

  onClick = ({ clientX, clientY }) => {
    const {
      canvasHelpers: { eyeDrop, colorDataToHex },
      getSquareFromCoords,
      activeDraftCtx,
      setColors,
      squareCount,
      primaryColor,
    } = this.props;
    const { x, y } = getSquareFromCoords(clientX, clientY);
    if (x >= squareCount || y >= squareCount || x < 0 || y < 0) {
      return;
    }
    const colorData = eyeDrop(x, y, activeDraftCtx);
    if (!colorData) {
      setColors(colorData);
      return;
    }
    const color = colorDataToHex(colorData);
    if (color !== primaryColor) {
      setColors(color);
    }
  };

  render() {
    const {
      paletteWidth,
      paletteHeight,
      squareCount,
      imageSize,
      paletteHelperCanvasRef,
    } = this.props;
    const eventListeners = {
      onMouseMove: this.onMouseMove,
      onMouseDown: this.onMouseDown,
      onMouseUp: this.onMouseUp,
      onMouseLeave: this.onMouseLeave,
      onClick: this.onClick,
    };
    return (
      <Canvas
        eventListeners={eventListeners}
        paletteCanvasRef={e => {
          this.paletteCanvas = e;
        }}
        paletteHelperCanvasRef={paletteHelperCanvasRef}
        paletteWidth={paletteWidth}
        paletteHeight={paletteHeight}
        squareCount={squareCount}
        imageSize={imageSize}
      />
    );
  }
}

export default EyeDrop;
