import React, { PureComponent, Fragment } from 'react';
import PropTypes from 'prop-types';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlus } from '@fortawesome/free-solid-svg-icons';
import genID from 'shortid';
import './Frames.scss';
import Frame from '../Frame/Frame';
import DragFrameBox from './DragFrameBox';
import FutureFrameElem from './FutureFrameElem';

class Frames extends PureComponent {
  static propTypes = {
    frameDrafts: PropTypes.arrayOf(
      PropTypes.shape({
        id: PropTypes.string.isRequired,
      }),
    ),
    addToFrameContexts: PropTypes.func.isRequired,
    activeFrameIndex: PropTypes.number,
    framesCount: PropTypes.number.isRequired,
    addNewFrameDraft: PropTypes.func.isRequired,
    makeFrameActive: PropTypes.func.isRequired,
    deleteFrameDraft: PropTypes.func.isRequired,
    copyFrameDraft: PropTypes.func.isRequired,
    moveFrameDraft: PropTypes.func.isRequired,
  };

  static defaultProps = {
    frameDrafts: [],
    activeFrameIndex: 0,
  };

  state = {
    isDragging: false,
    dragIndex: null,
    insertIndex: null,
    dragFrameSize: null,
    topOffset: null,
    leftOffset: null,
    originalY: null,
    originalOffset: null,
  };

  futureFrameID = genID();

  draggingUpward = null;

  TRIGGER_DIFFERENCE = 40;

  componentDidMount() {
    const { addNewFrameDraft } = this.props;
    addNewFrameDraft();
  }

  handleOptionClick = (option, frame) => {
    const frameIndex =
      Number(frame.querySelector('.frame__index').innerHTML) - 1;
    if (option.classList.contains('frame__option--delete')) {
      const { deleteFrameDraft } = this.props;
      deleteFrameDraft(frameIndex);
    } else if (option.classList.contains('frame__option--copy')) {
      const { copyFrameDraft } = this.props;
      copyFrameDraft(frameIndex);
    }
  };

  startDragging = (frame, originalY) => {
    document.addEventListener('mousemove', this.onMouseMove);
    document.addEventListener('mouseup', this.onMouseUp);

    const frameIndex =
      Number(frame.querySelector('.frame__index').innerHTML) - 1;
    const frameSize = frame.clientHeight;
    const frameOffsets = frame.getBoundingClientRect();
    const frameTopOffet = frameOffsets.top;
    const frameLeftOffet = frameOffsets.left;
    this.setState({
      isDragging: true,
      dragIndex: frameIndex,
      insertIndex: frameIndex,
      dragFrameSize: frameSize,
      topOffset: frameTopOffet,
      leftOffset: frameLeftOffet,
      originalY,
      originalOffset: frameTopOffet,
    });
  };

  finishDragging = () => {
    const { moveFrameDraft, framesCount } = this.props;
    const { dragIndex } = this.state;
    let { insertIndex } = this.state;
    document.removeEventListener('mousemove', this.onMouseMove);
    document.removeEventListener('mouseup', this.onMouseUp);
    this.setState({
      isDragging: false,
      dragIndex: null,
      insertIndex: null,
      dragFrameSize: null,
      topOffset: null,
      leftOffset: null,
      originalY: null,
      originalOffset: null,
    });
    if (insertIndex >= framesCount) {
      insertIndex = framesCount - 1;
    }
    moveFrameDraft(dragIndex, insertIndex);
  };

  makeFrameActive = frame => {
    const { makeFrameActive } = this.props;
    const frameIndex =
      Number(frame.querySelector('.frame__index').innerHTML) - 1;
    makeFrameActive(frameIndex);
  };

  checkInsertFrame = () => {
    const { framesCount } = this.props;
    const { topOffset: dragFrameTop, insertIndex, dragFrameSize } = this.state;
    if (framesCount === 1) {
      return;
    }
    const futureFrameTop = this.futureFrameElement.getBoundingClientRect().top;
    const differenceOfTops = dragFrameTop - futureFrameTop;
    let newInsertIndex;
    if (differenceOfTops > this.TRIGGER_DIFFERENCE && !this.draggingUpward) {
      newInsertIndex = insertIndex + 1;
    } else if (differenceOfTops < -1 * dragFrameSize && this.draggingUpward) {
      newInsertIndex = insertIndex - 1;
    } else {
      return;
    }
    if (newInsertIndex < 0 || newInsertIndex > framesCount) {
      return;
    }
    this.setState({
      insertIndex: newInsertIndex,
    });
  };

  onClick = e => {
    const { target } = e;
    const option = target.closest('.frame__option');
    const frame = target.closest('.frame');
    if (option) {
      this.handleOptionClick(option, frame);
    } else if (frame) {
      this.makeFrameActive(frame);
    }
  };

  onMouseDown = ({ target, clientY }) => {
    const frame = target.closest('.frame');
    const dragIcon = target.closest('.frame__option--move');
    if (!dragIcon) {
      return;
    }
    this.startDragging(frame, clientY);
  };

  onMouseMove = ({ clientY }) => {
    this.setState(
      ({ originalY, originalOffset, topOffset: currentTopOffset }) => {
        const newTopOffset = originalOffset + -1 * (originalY - clientY);
        this.draggingUpward = newTopOffset - currentTopOffset < 0;
        return { topOffset: newTopOffset };
      },
      this.checkInsertFrame,
    );
  };

  onMouseUp = () => {
    const { isDragging } = this.state;
    if (isDragging) {
      this.finishDragging();
    }
  };

  render() {
    const {
      isDragging,
      dragIndex,
      insertIndex,
      dragFrameSize,
      topOffset,
      leftOffset,
    } = this.state;
    const {
      frameDrafts,
      activeFrameIndex,
      addNewFrameDraft,
      framesCount,
      addToFrameContexts,
    } = this.props;

    let futureFrameInserted = false;

    return (
      <section
        role="presentation"
        onMouseDown={this.onMouseDown}
        onClick={this.onClick}
        onDragStart={e => {
          e.preventDefault();
        }}
        className="frames"
      >
        {frameDrafts.map(({ id }, index) => {
          const isActive = activeFrameIndex === index;

          if (isDragging && index === insertIndex && index === dragIndex) {
            futureFrameInserted = true;
            return (
              <FutureFrameElem
                key={this.futureFrameID}
                dragFrameSize={dragFrameSize}
                elemRef={e => {
                  this.futureFrameElement = e;
                }}
              />
            );
          }

          if (isDragging && index === dragIndex && index !== insertIndex) {
            return '';
          }

          if (isDragging && index === insertIndex && !futureFrameInserted) {
            futureFrameInserted = true;
            return (
              <Fragment>
                <FutureFrameElem
                  key={this.futureFrameID}
                  dragFrameSize={dragFrameSize}
                  elemRef={e => {
                    this.futureFrameElement = e;
                  }}
                />
                <Frame
                  key={id}
                  index={index}
                  isActive={isActive}
                  framesCount={framesCount}
                  addToFrameContexts={addToFrameContexts}
                />
              </Fragment>
            );
          }

          return (
            <Frame
              key={id}
              index={index}
              isActive={isActive}
              framesCount={framesCount}
              addToFrameContexts={addToFrameContexts}
            />
          );
        })}
        {!futureFrameInserted && isDragging ? (
          <FutureFrameElem
            key={this.futureFrameID}
            dragFrameSize={dragFrameSize}
            elemRef={e => {
              this.futureFrameElement = e;
            }}
          />
        ) : (
          ''
        )}
        {isDragging ? (
          <DragFrameBox
            framesCount={framesCount}
            addToFrameContexts={addToFrameContexts}
            activeFrameIndex={activeFrameIndex}
            dragIndex={dragIndex}
            topOffset={topOffset}
            leftOffset={leftOffset}
          />
        ) : (
          ''
        )}
        <button
          onClick={addNewFrameDraft}
          className="frames__button"
          type="button"
        >
          <FontAwesomeIcon className="frames__button-icon" icon={faPlus} />
          <span className="frames__button-text">Add new frame</span>
        </button>
      </section>
    );
  }
}

export default Frames;
