import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faTrashAlt,
  faArrowsAlt,
  faCopy,
} from '@fortawesome/free-solid-svg-icons';
import './Frame.scss';

class Frame extends PureComponent {
  static propTypes = {
    addToFrameContexts: PropTypes.func.isRequired,
    index: PropTypes.number.isRequired,
    isActive: PropTypes.bool.isRequired,
    framesCount: PropTypes.number.isRequired,
  };

  componentDidMount() {
    const { addToFrameContexts, index } = this.props;
    const ctx = this.frameCanvas.getContext('2d');
    addToFrameContexts(ctx, index);
  }

  render() {
    const { index, isActive, framesCount } = this.props;
    const activeClass = isActive ? 'frame--active' : '';

    const deleteButton =
      framesCount === 1 ? (
        ''
      ) : (
        <span className="frame__option frame__option--delete">
          <FontAwesomeIcon icon={faTrashAlt} />
        </span>
      );

    return (
      <div className={`frame ${activeClass}`}>
        <canvas
          ref={e => {
            this.frameCanvas = e;
          }}
          width="96"
          height="96"
          className="frames__canvas"
        />
        <span className="frame__index">{index + 1}</span>
        {deleteButton}
        <span className="frame__option frame__option--move">
          <FontAwesomeIcon icon={faArrowsAlt} />
        </span>
        <span className="frame__option frame__option--copy">
          <FontAwesomeIcon icon={faCopy} />
        </span>
      </div>
    );
  }
}

export default Frame;
