import React, { Component } from 'react';
import PropTypes from 'prop-types';

class SaveSetting extends Component {
  static propTypes = {
    createSaveFileContents: PropTypes.func.isRequired,
    muteSettings: PropTypes.func.isRequired,
    localStorageKey: PropTypes.string.isRequired,
    renderMessageBox: PropTypes.func.isRequired,
  };

  state = {
    piskelFileName: 'New Piskel (Clone)',
  };

  fileNameInput = null;

  saveAsFileButton = null;

  saveInBrowserButton = null;

  onClick = ({ target }) => {
    if (target === this.saveAsFileButton) {
      this.saveProjectAsFile();
    } else if (target === this.saveInBrowserButton) {
      this.saveProjectInBrowser();
    }
  };

  saveProjectAsFile = () => {
    const { piskelFileName } = this.state;
    if (!piskelFileName) {
      return;
    }

    const { createSaveFileContents, muteSettings } = this.props;
    const contents = createSaveFileContents();
    const blob = new Blob([contents]);
    const link = document.createElement('a');
    const url = URL.createObjectURL(blob);

    link.href = url;
    link.hidden = true;
    link.download = `${piskelFileName}.piskel`;

    document.body.append(link);
    link.click();
    muteSettings();

    setTimeout(() => {
      URL.revokeObjectURL(url);
      link.remove();
    });
  };

  saveProjectInBrowser = () => {
    const { piskelFileName } = this.state;
    const { muteSettings, renderMessageBox } = this.props;
    if (!piskelFileName) {
      return;
    }

    const { createSaveFileContents, localStorageKey } = this.props;
    const contents = createSaveFileContents();

    localStorage.setItem(localStorageKey, contents);
    muteSettings();
    renderMessageBox('Saved Successfully');
  };

  handleFileNameChange = () => {
    const { piskelFileName } = this.state;
    const newPiskelFileName = this.fileNameInput.value;
    if (newPiskelFileName === piskelFileName) {
      return;
    }
    this.setState({
      piskelFileName: newPiskelFileName,
    });
  };

  render() {
    const { piskelFileName } = this.state;
    return (
      <div
        role="presentation"
        onClick={this.onClick}
        className="settings__main active-setting-block"
      >
        <h1 className="active-setting-block__header">Sprite Information</h1>
        <p className="active-setting-block__row">
          <span className="active-setting-block__text">Title :</span>
        </p>
        <input
          onChange={this.handleFileNameChange}
          ref={e => {
            this.fileNameInput = e;
          }}
          value={piskelFileName}
          className="active-setting-block__input"
          type="text"
        />
        <h1 className="active-setting-block__header">Save offline as file</h1>
        <p className="active-setting-block__row">
          <button
            ref={e => {
              this.saveAsFileButton = e;
            }}
            type="button"
            className="active-setting-block__button"
          >
            Save as .piskel
          </button>
        </p>
        <p className="active-setting-block__row">
          <span className="active-setting-block__text active-setting-block__text--italic">
            Your sprite will be downloaded as a .piskel file
          </span>
        </p>
        <h1 className="active-setting-block__header">
          Save offline in browser
        </h1>
        <p className="active-setting-block__row">
          <button
            ref={e => {
              this.saveInBrowserButton = e;
            }}
            type="button"
            className="active-setting-block__button"
          >
            Save in Browser
          </button>
        </p>
        <p className="active-setting-block__row">
          <span className="active-setting-block__text active-setting-block__text--italic">
            Your piskel will be saved locally and will only be accessible from
            this browser.
          </span>
        </p>
      </div>
    );
  }
}

export default SaveSetting;
