import React, { PureComponent } from 'react';
import ReactDOM from 'react-dom';
import PropTypes from 'prop-types';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSyncAlt } from '@fortawesome/free-solid-svg-icons';
import { SketchPicker } from 'react-color';
import './ColorPick.scss';

const ColorPickPortal = ({ children }) =>
  ReactDOM.createPortal(children, document.body);

ColorPickPortal.propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.element,
    PropTypes.arrayOf(PropTypes.element),
  ]).isRequired,
};

class ColorPick extends PureComponent {
  static propTypes = {
    primaryColor: PropTypes.string,
    secondaryColor: PropTypes.string,
    setColors: PropTypes.func.isRequired,
  };

  static defaultProps = {
    primaryColor: null,
    secondaryColor: null,
  };

  state = {
    pickerVisible: false,
  };

  colorTypesToSet = {
    primary: true,
    secondary: false,
  };

  defaultColorPickerColor = '#000000';

  componentDidMount() {
    document.addEventListener('click', this.toggleColorPicker);
  }

  componentWillUnmount() {
    document.removeEventListener('click', this.toggleColorPicker);
  }

  switchClick = ({ target }) => {
    const switchElem = target.closest('.color-pick__switch');
    if (!switchElem) {
      return;
    }
    const { primaryColor, secondaryColor, setColors } = this.props;
    setColors(secondaryColor, primaryColor);
  };

  handleChange = ({ hex }) => {
    const { primaryColor, secondaryColor } = this.props;
    const { setColors } = this.props;
    if (this.colorTypesToSet.primary) {
      setColors(hex, secondaryColor);
    } else {
      setColors(primaryColor, hex);
    }
  };

  toggleColorPicker = ({ target }) => {
    const colorPickerClicked = target.closest('.color-pick__picker');
    const colorSwapClicked = target.closest('.color-pick__color');
    if (!colorSwapClicked && !colorPickerClicked) {
      this.hideColorPicker();
    } else if (colorSwapClicked) {
      this.showColorPickerNextToColor(colorSwapClicked);
    }
  };

  hideColorPicker = () => {
    this.setState({ pickerVisible: false });
  };

  showColorPickerNextToColor = colorSwap => {
    if (colorSwap.classList.contains('color-pick__color--secondary')) {
      this.colorTypesToSet.primary = false;
      this.colorTypesToSet.secondary = true;
    } else if (colorSwap.classList.contains('color-pick__color--primary')) {
      this.colorTypesToSet.primary = true;
      this.colorTypesToSet.secondary = false;
    }
    const coords = colorSwap.getBoundingClientRect();
    this.colorPickerBox.style.top = `${coords.top}px`;
    this.colorPickerBox.style.left = `${coords.left}px`;
    this.setState({ pickerVisible: true });
  };

  onClick = e => {
    this.switchClick(e);
  };

  render() {
    const { pickerVisible } = this.state;
    const { primaryColor, secondaryColor } = this.props;
    let color;
    if (this.colorTypesToSet.primary) {
      color = primaryColor || this.defaultColorPickerColor;
    } else {
      color = secondaryColor || this.defaultColorPickerColor;
    }
    return (
      <div role="presentation" onClick={this.onClick} className="color-pick">
        <div
          style={{ background: secondaryColor || '' }}
          className="color-pick__color color-pick__color--secondary"
        />
        <div
          style={{ background: primaryColor || '' }}
          className="color-pick__color color-pick__color--primary"
        />
        <FontAwesomeIcon className="color-pick__switch" icon={faSyncAlt} />
        <ColorPickPortal>
          <div
            style={{ visibility: pickerVisible ? 'visible' : 'hidden' }}
            ref={e => {
              this.colorPickerBox = e;
            }}
            className="color-pick__picker"
            onMouseLeave={this.hideColorPicker}
          >
            <SketchPicker
              color={color}
              onChange={this.handleChange}
              presetColors={[]}
              disableAlpha
            />
          </div>
        </ColorPickPortal>
      </div>
    );
  }
}

export default ColorPick;
