import React from 'react';
import PropTypes from 'prop-types';

const FutureFrameElem = ({ dragFrameSize, elemRef }) => (
  <div
    style={{
      width: `${dragFrameSize}px`,
      height: `${dragFrameSize}px`,
    }}
    className="frames__future-frame"
    ref={elemRef}
  />
);

FutureFrameElem.propTypes = {
  dragFrameSize: PropTypes.number.isRequired,
  elemRef: PropTypes.func.isRequired,
};

export default FutureFrameElem;
