import React, { Fragment } from 'react';
import PropTypes from 'prop-types';

const Canvas = ({
  paletteCanvasRef,
  paletteHelperCanvasRef,
  paletteWidth,
  paletteHeight,
  eventListeners,
  squareCount,
  imageSize,
}) => (
  <Fragment>
    <canvas
      className="palette__canvas"
      ref={paletteCanvasRef}
      width={paletteWidth}
      height={paletteHeight}
    />
    <canvas
      width={squareCount}
      height={squareCount}
      style={{ width: `${imageSize}px`, height: `${imageSize}px` }}
      ref={paletteHelperCanvasRef}
      className="palette__helper-canvas"
      {...eventListeners}
    />
  </Fragment>
);

Canvas.propTypes = {
  paletteCanvasRef: PropTypes.func.isRequired,
  paletteHelperCanvasRef: PropTypes.func.isRequired,
  paletteWidth: PropTypes.number.isRequired,
  paletteHeight: PropTypes.number.isRequired,
  imageSize: PropTypes.number.isRequired,
  squareCount: PropTypes.number.isRequired,
  eventListeners: PropTypes.shape({}),
};

Canvas.defaultProps = {
  eventListeners: {},
};

export default Canvas;
