import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import Canvas from '../Canvas/Canvas';

class Eraser extends PureComponent {
  static propTypes = {
    updateActiveFrameCanvas: PropTypes.func.isRequired,
    getSquareFromCoords: PropTypes.func.isRequired,
    updatePaletteCanvas: PropTypes.func.isRequired,
    activeDraftCtx: PropTypes.shape({
      canvas: PropTypes.shape({
        width: PropTypes.number.isRequired,
        height: PropTypes.number.isRequired,
      }).isRequired,
    }),
    canvasHelpers: PropTypes.shape({
      clearRectangleAt: PropTypes.func.isRequired,
      bresenhamsAlgorithm: PropTypes.func.isRequired,
    }).isRequired,
    paletteWidth: PropTypes.number.isRequired,
    paletteHeight: PropTypes.number.isRequired,
    imageSize: PropTypes.number.isRequired,
    squareCount: PropTypes.number.isRequired,
    paletteHelperCanvasRef: PropTypes.func.isRequired,
    onMount: PropTypes.func.isRequired,
    updateHelperCanvas: PropTypes.func.isRequired,
    sharedMouseMove: PropTypes.func.isRequired,
    sharedMouseDown: PropTypes.func.isRequired,
    sharedMouseUp: PropTypes.func.isRequired,
    sharedMouseLeave: PropTypes.func.isRequired,
  };

  static defaultProps = {
    activeDraftCtx: null,
  };

  paletteCtx = null;

  isErasing = false;

  prevSquare = null;

  componentDidMount() {
    this.init();
    const { onMount, updateHelperCanvas } = this.props;
    onMount(this.paletteCtx);
    updateHelperCanvas();
  }

  componentDidUpdate() {
    const { updateHelperCanvas } = this.props;
    updateHelperCanvas();
  }

  init = () => {
    this.paletteCtx = this.paletteCanvas.getContext('2d');
  };

  startErasing = ({ clientX, clientY }) => {
    const {
      canvasHelpers: { clearRectangleAt },
      updatePaletteCanvas,
      getSquareFromCoords,
      activeDraftCtx,
    } = this.props;
    this.isErasing = true;
    const square = getSquareFromCoords(clientX, clientY);
    this.prevSquare = square;
    clearRectangleAt(square.x, square.y, activeDraftCtx, this.paintColor);
    updatePaletteCanvas();
  };

  erase = ({ clientX, clientY }) => {
    const {
      canvasHelpers: { clearRectangleAt, bresenhamsAlgorithm },
      activeDraftCtx,
      updatePaletteCanvas,
      getSquareFromCoords,
    } = this.props;
    if (!this.isErasing) return;
    const square = getSquareFromCoords(clientX, clientY);
    if (square.x === this.prevSquare.x && square.y === this.prevSquare.y) {
      return;
    }
    bresenhamsAlgorithm(
      square,
      this.prevSquare,
      clearRectangleAt,
      activeDraftCtx,
    );
    this.prevSquare = square;
    updatePaletteCanvas();
  };

  finishErasing = () => {
    if (!this.isErasing) {
      return;
    }
    const { updateActiveFrameCanvas } = this.props;
    this.isErasing = false;
    this.prevSquare = null;
    updateActiveFrameCanvas();
  };

  onMouseMove = e => {
    const { sharedMouseMove } = this.props;
    sharedMouseMove(e);
    this.erase(e);
  };

  onMouseDown = e => {
    const { sharedMouseDown } = this.props;
    sharedMouseDown();
    this.startErasing(e);
  };

  onMouseUp = e => {
    const { sharedMouseUp } = this.props;
    sharedMouseUp(e);
    this.finishErasing();
  };

  onMouseLeave = () => {
    const { sharedMouseLeave } = this.props;
    sharedMouseLeave();
    this.finishErasing();
  };

  render() {
    const {
      paletteWidth,
      paletteHeight,
      squareCount,
      imageSize,
      paletteHelperCanvasRef,
    } = this.props;
    const eventListeners = {
      onMouseMove: this.onMouseMove,
      onMouseDown: this.onMouseDown,
      onMouseUp: this.onMouseUp,
      onMouseLeave: this.onMouseLeave,
    };
    return (
      <Canvas
        eventListeners={eventListeners}
        paletteCanvasRef={e => {
          this.paletteCanvas = e;
        }}
        paletteHelperCanvasRef={paletteHelperCanvasRef}
        paletteWidth={paletteWidth}
        paletteHeight={paletteHeight}
        squareCount={squareCount}
        imageSize={imageSize}
      />
    );
  }
}

export default Eraser;
