import React, { PureComponent } from 'react';
import './Preview.scss';
import PropTypes from 'prop-types';
import genID from 'shortid';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faWindowMaximize } from '@fortawesome/free-solid-svg-icons';

class Preview extends PureComponent {
  static propTypes = {
    onMount: PropTypes.func.isRequired,
    squareCount: PropTypes.number.isRequired,
    framesCount: PropTypes.number.isRequired,
    imageSize: PropTypes.number.isRequired,
    currentFPS: PropTypes.number.isRequired,
    setNewFPS: PropTypes.func.isRequired,
    imageURLs: PropTypes.arrayOf(PropTypes.string),
    activeFrameIndex: PropTypes.number,
  };

  static defaultProps = {
    imageURLs: ['data:,'],
    activeFrameIndex: -1,
  };

  state = {
    activeSquare: null,
    animationImageZoom: 'full',
    zoomOptionsList: [
      {
        id: genID(),
        imageZoom: 1,
        innerHTML: '1x',
        CSSclass: '',
      },
      {
        id: genID(),
        imageZoom: 'full',
        innerHTML: 'Full',
        CSSclass: 'animation__option--popup',
      },
      {
        id: genID(),
        imageZoom: 2,
        innerHTML: '2x',
        CSSclass: '',
      },
    ],
  };

  MILLISEC_IN_SECOND = 1000;

  animationImageNumber = 0;

  intervalID = null;

  animationScreen = null;

  fpsInput = null;

  componentDidMount() {
    const { onMount, currentFPS } = this.props;
    onMount(this.updateActiveSquare);
    this.setFPSto(currentFPS);
  }

  componentDidUpdate({ currentFPS: prevFPS }) {
    const { currentFPS } = this.props;
    if (currentFPS !== prevFPS) {
      this.setFPSto(currentFPS);
    }
  }

  componentWillUnmount() {
    this.setFPSto(0);
  }

  updateActiveSquare = square => {
    this.setState({ activeSquare: square });
  };

  setFPSto = newFPSValue => {
    const changeRate = Math.floor(this.MILLISEC_IN_SECOND / newFPSValue);
    clearInterval(this.intervalID);
    if (newFPSValue === 0) {
      return;
    }
    this.intervalID = setInterval(() => {
      const { framesCount } = this.props;
      this.updateAnimationImage();
      this.animationImageNumber = (this.animationImageNumber + 1) % framesCount;
    }, changeRate);
  };

  handleFPSChange = () => {
    const { setNewFPS } = this.props;
    const newFPSVal = Number(this.fpsInput.value);
    setNewFPS(newFPSVal);
  };

  updateAnimationImage = () => {
    const { imageURLs } = this.props;
    const imageURL = imageURLs[this.animationImageNumber];
    if (!imageURL) {
      return;
    }
    this.animationScreen.style.backgroundImage = `url("${imageURL}")`;
  };

  handleOptionsClick = ({ target }) => {
    const { zoomOptionsList } = this.state;
    const option = target.closest('.animation__option');
    if (!option) {
      return;
    }
    const innerHTML = option.innerHTML.trim();
    const zoomOptionsItem = zoomOptionsList.find(
      item => item.innerHTML === innerHTML,
    );
    if (zoomOptionsItem) {
      this.setState({
        animationImageZoom: zoomOptionsItem.imageZoom,
      });
    }
  };

  render() {
    const { activeSquare, animationImageZoom, zoomOptionsList } = this.state;
    const {
      activeFrameIndex,
      framesCount,
      squareCount,
      imageSize,
      currentFPS,
    } = this.props;

    let backgroundSize;
    if (animationImageZoom === 'full') {
      backgroundSize = '100%';
    } else {
      backgroundSize = `${squareCount * animationImageZoom}px`;
    }

    const squareData = activeSquare
      ? `  ${activeSquare.x}:${activeSquare.y}`
      : '';
    let zoom = imageSize / squareCount;
    if (zoom < 10) {
      zoom = zoom.toPrecision(3);
    } else {
      zoom = zoom.toPrecision(4);
    }
    return (
      <section className="preview">
        <div className="animation">
          <div className="animation__cover">
            <div
              style={{
                backgroundSize,
              }}
              className="animation__screen"
              ref={e => {
                this.animationScreen = e;
              }}
            />
          </div>
          <div className="animation__fps-container">
            <span className="animation__fps-text">{`${currentFPS} FPS`}</span>
            <input
              className="animation__fps-range"
              ref={e => {
                this.fpsInput = e;
              }}
              onChange={this.handleFPSChange}
              type="range"
              value={currentFPS}
              min="0"
              max="24"
            />
          </div>
          <div
            role="presentation"
            onClick={this.handleOptionsClick}
            className="animation__settings"
          >
            <div className="settings-drop-down">
              {zoomOptionsList.map(({ id, innerHTML, imageZoom, CSSclass }) => {
                const isActive = imageZoom === animationImageZoom;
                return (
                  <div
                    key={id}
                    className={`settings-drop-down__item animation__option ${
                      isActive ? 'settings-drop-down__item--active' : ''
                    } ${CSSclass}`}
                  >
                    {innerHTML}
                  </div>
                );
              })}
            </div>
            <div className="animation__option">
              <FontAwesomeIcon icon={faWindowMaximize} />
            </div>
          </div>
        </div>
        <div className="preview__data">
          <div className="preview__date-row">
            <span className="preview__data-text">{`x${zoom}`}</span>
          </div>
          <div className="preview__date-row">
            <span className="preview__data-text">{`[${squareCount}x${squareCount}]`}</span>
            <span className="preview__data-text">{squareData}</span>
          </div>
          <div className="preview__date-row">
            <span className="preview__data-text">
              {`${activeFrameIndex + 1}/${framesCount}`}
            </span>
          </div>
        </div>
      </section>
    );
  }
}

export default Preview;
