import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import './Palette.scss';

class Palette extends PureComponent {
  static propTypes = {
    squareCount: PropTypes.number.isRequired,
    updateActiveFrameCanvas: PropTypes.func.isRequired,
    activeDraftCtx: PropTypes.shape({
      canvas: PropTypes.shape({
        width: PropTypes.number.isRequired,
        height: PropTypes.number.isRequired,
      }).isRequired,
    }),
    activeTool: PropTypes.func.isRequired,
    canvasHelpers: PropTypes.shape({
      drawRectangleAt: PropTypes.func.isRequired,
      clearRectangleAt: PropTypes.func.isRequired,
      clearCanvas: PropTypes.func.isRequired,
      fillAnyCanvas: PropTypes.func.isRequired,
      eyeDrop: PropTypes.func.isRequired,
      getLightness: PropTypes.func.isRequired,
      lightenColor: PropTypes.func.isRequired,
      darknenColor: PropTypes.func.isRequired,
      bresenhamsAlgorithm: PropTypes.func.isRequired,
      floodFillAlgorithm: PropTypes.func.isRequired,
      hexToData: PropTypes.func.isRequired,
      colorDataToHex: PropTypes.func.isRequired,
    }).isRequired,
    paletteClassName: PropTypes.string.isRequired,
    setColors: PropTypes.func.isRequired,
    primaryColor: PropTypes.string,
    setActiveSquare: PropTypes.func.isRequired,
    imageSize: PropTypes.number.isRequired,
    updateImageSize: PropTypes.func.isRequired,
  };

  static defaultProps = {
    primaryColor: null,
    activeDraftCtx: null,
  };

  state = {
    paletteWidth: 0,
    paletteHeight: 0,
    squareSize: 0,
    paletteCanvasOffsets: {
      top: 0,
      left: 0,
    },
    imageMargins: {
      top: 0,
      left: 0,
    },
  };

  BACK_COLOR = '#cccccc';

  step = 20;

  defaultHighlightColor = '#00000040';

  activeSquare = null;

  toolEnabled = false;

  maxAllowedCustomSize = null;

  minAllowedCustomSize = null;

  paletteCtx = null;

  paletteHelperCtx = null;

  componentDidMount() {
    window.addEventListener('resize', this.init);
    this.init();
  }

  componentDidUpdate({ squareCount: prevSquareCount }) {
    const { squareCount } = this.props;
    this.updatePaletteCanvas();
    if (squareCount !== prevSquareCount) {
      this.init();
    }
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.init);
  }

  updateHelperCanvas = () => {
    this.paletteHelperCtx = this.paletteHelperCanvas.getContext('2d');
  };

  init = () => {
    const { squareCount, updateImageSize } = this.props;
    const paletteWidth = this.paletteElem.clientWidth;
    const paletteHeight = this.paletteElem.clientHeight;
    const imageSize = Math.min(paletteWidth, paletteHeight);
    const leftImageMargin = (paletteWidth - imageSize) / 2;
    const topImageMargin = (paletteHeight - imageSize) / 2;
    const squareSize = imageSize / squareCount;
    updateImageSize(imageSize);
    this.setState(
      {
        paletteWidth,
        paletteHeight,
        squareSize,
        imageMargins: {
          top: topImageMargin,
          left: leftImageMargin,
        },
        lowerBound: squareCount,
        upperBound: imageSize * 5,
      },
      () => {
        const paletteCanvasCoords = this.paletteCtx.canvas.getBoundingClientRect();
        this.setState({
          paletteCanvasOffsets: {
            top: paletteCanvasCoords.top,
            left: paletteCanvasCoords.left,
          },
        });
      },
    );
  };

  getSquareFromCoords = (clientX, clientY) => {
    const {
      paletteCanvasOffsets: { top, left },
      imageMargins: { top: marginTop, left: marginLeft },
      squareSize,
    } = this.state;
    const topOffset = top + marginTop;
    const leftOffset = left + marginLeft;
    return {
      x: Math.floor((clientX - leftOffset) / squareSize),
      y: Math.floor((clientY - topOffset) / squareSize),
    };
  };

  updatePaletteCanvas = (sizeChanged = true) => {
    const {
      imageMargins: { top: topMargin, left: leftMargin },
    } = this.state;
    const {
      canvasHelpers: { clearCanvas },
      activeDraftCtx,
      imageSize,
    } = this.props;
    if (!activeDraftCtx) return;
    if (sizeChanged) {
      clearCanvas(this.paletteCtx);
      this.paletteCtx.fillStyle = this.BACK_COLOR;
      this.paletteCtx.fillRect(
        0,
        0,
        this.paletteCtx.canvas.width,
        this.paletteCtx.canvas.height,
      );
    }
    this.paletteCtx.clearRect(leftMargin, topMargin, imageSize, imageSize);
    this.paletteCtx.imageSmoothingEnabled = false;
    this.paletteCtx.drawImage(
      activeDraftCtx.canvas,
      leftMargin,
      topMargin,
      imageSize,
      imageSize,
    );
  };

  resizeImage = deltaY => {
    const increase = deltaY < 0;
    const { squareCount, updateImageSize } = this.props;
    let { imageSize } = this.props;
    const { lowerBound, upperBound, paletteWidth, paletteHeight } = this.state;
    if (imageSize === lowerBound && increase) {
      imageSize = this.minAllowedCustomSize;
    } else if (imageSize === upperBound && !increase) {
      imageSize = this.maxAllowedCustomSize;
    } else if (imageSize !== lowerBound && imageSize !== upperBound) {
      if (increase && imageSize + this.step * 1.5 >= upperBound) {
        this.maxAllowedCustomSize = imageSize;
        imageSize = upperBound;
      } else if (!increase && imageSize - this.step * 1.5 <= lowerBound) {
        this.minAllowedCustomSize = imageSize;
        imageSize = lowerBound;
      } else if (increase) {
        imageSize += this.step;
      } else {
        imageSize -= this.step;
      }
    }
    const leftImageMargin = (paletteWidth - imageSize) / 2;
    const topImageMargin = (paletteHeight - imageSize) / 2;
    updateImageSize(imageSize);
    this.setState({
      squareSize: imageSize / squareCount,
      imageMargins: {
        top: topImageMargin,
        left: leftImageMargin,
      },
    });
  };

  handleHover = (x, y) => {
    const {
      activeDraftCtx,
      setActiveSquare,
      canvasHelpers: { eyeDrop },
      squareCount,
    } = this.props;
    const square = this.getSquareFromCoords(x, y);
    if (
      square.x < 0 ||
      square.y < 0 ||
      square.x >= squareCount ||
      square.y >= squareCount
    ) {
      return;
    }
    const { activeSquare } = this;
    if (
      activeSquare &&
      activeSquare.x === square.x &&
      activeSquare.y === square.y
    ) {
      return;
    }
    setActiveSquare(square);
    if (this.toolEnabled) {
      return;
    }
    const originalColor = eyeDrop(square.x, square.y, activeDraftCtx);
    let highlightColor;
    if (!originalColor) {
      highlightColor = this.defaultHighlightColor;
    } else {
      highlightColor = this.pickHightlightColor(originalColor);
    }
    if (this.activeSquare) {
      this.blurActiveSquare();
    }
    this.activeSquare = square;
    this.highlightActiveSquare(highlightColor);
  };

  highlightActiveSquare = color => {
    if (!this.activeSquare || !this.paletteHelperCtx) {
      return;
    }
    const { x, y } = this.activeSquare;
    this.paletteHelperCtx.fillStyle = color;
    this.paletteHelperCtx.fillRect(x, y, 1, 1);
  };

  blurActiveSquare = () => {
    const { x, y } = this.activeSquare;
    this.paletteHelperCtx.clearRect(x, y, 1, 1);
  };

  pickHightlightColor = colorData => {
    const {
      canvasHelpers: { getLightness, lightenColor, darknenColor },
    } = this.props;
    if (colorData[3] === 0) {
      return this.defaultHighlightColor;
    }
    const lightness = getLightness(colorData);
    let highlightColor;
    if (lightness > 50) {
      highlightColor = darknenColor(colorData);
    } else {
      highlightColor = lightenColor(colorData);
    }
    return highlightColor;
  };

  onMouseMove = ({ clientX, clientY }) => {
    this.handleHover(clientX, clientY);
  };

  onMouseDown = () => {
    if (this.activeSquare) {
      this.blurActiveSquare();
    }
    this.toolEnabled = true;
  };

  onMouseUp = ({ clientX, clientY }) => {
    this.toolEnabled = false;
    this.handleHover(clientX, clientY);
  };

  onMouseLeave = () => {
    const { setActiveSquare } = this.props;
    if (this.toolEnabled) {
      this.toolEnabled = false;
    }
    if (this.activeSquare) {
      this.blurActiveSquare();
      this.activeSquare = null;
      setActiveSquare(null);
    }
  };

  onWheel = ({ deltaY }) => {
    this.resizeImage(deltaY);
  };

  render() {
    const { paletteWidth, paletteHeight } = this.state;
    const {
      activeTool,
      updateActiveFrameCanvas,
      canvasHelpers,
      activeDraftCtx,
      paletteClassName,
      setColors,
      primaryColor,
      squareCount,
      imageSize,
    } = this.props;
    const ToolComp = activeTool;
    return (
      <section
        onWheel={this.onWheel}
        ref={e => {
          this.paletteElem = e;
        }}
        className={`palette ${paletteClassName}`}
      >
        <ToolComp
          activeDraftCtx={activeDraftCtx}
          updateActiveFrameCanvas={updateActiveFrameCanvas}
          updatePaletteCanvas={this.updatePaletteCanvas}
          getSquareFromCoords={this.getSquareFromCoords}
          canvasHelpers={canvasHelpers}
          paletteWidth={paletteWidth}
          paletteHeight={paletteHeight}
          imageSize={imageSize}
          onMount={paletteCtx => {
            this.paletteCtx = paletteCtx;
          }}
          paletteHelperCanvasRef={e => {
            this.paletteHelperCanvas = e;
          }}
          updateHelperCanvas={this.updateHelperCanvas}
          setColors={setColors}
          primaryColor={primaryColor}
          squareCount={squareCount}
          sharedMouseMove={this.onMouseMove}
          sharedMouseDown={this.onMouseDown}
          sharedMouseUp={this.onMouseUp}
          sharedMouseLeave={this.onMouseLeave}
        />
      </section>
    );
  }
}

export default Palette;
