import React, { Component } from 'react';
import PropTypes from 'prop-types';
import genID from 'shortid';
import SaveAsGIF from './ExportOptions/SaveAsGIF';
import SaveAsPNG from './ExportOptions/SaveAsPNG';
import SaveAsOwnFormat from './ExportOptions/SaveAsOwnFormat';

class ExportSetting extends Component {
  static propTypes = {
    squareCount: PropTypes.number.isRequired,
    framesCount: PropTypes.number.isRequired,
  };

  state = {
    exportOption: [
      {
        id: genID(),
        key: 'gif',
        header: 'GIF',
        component: SaveAsGIF,
      },
      {
        id: genID(),
        key: 'png',
        header: 'PNG',
        component: SaveAsPNG,
      },
      {
        id: genID(),
        key: 'custom',
        header: 'Others',
        component: SaveAsOwnFormat,
      },
    ],
    activeExportOption: 'gif',
    exportResolution: null,
    exportImageScale: 1,
  };

  MAX_SCALE_TIMES = 32;

  scaleInput = null;

  static getDerivedStateFromProps({ squareCount }, { exportResolution }) {
    if (exportResolution === null) {
      return {
        exportResolution: squareCount,
      };
    }
    return {};
  }

  handleHeaderClick = header => {
    const headerKey = header.dataset.key;
    this.setState({
      activeExportOption: headerKey,
    });
  };

  handleScaleChange = () => {
    const { squareCount } = this.props;
    const newScaleValue = Number(this.scaleInput.value);
    this.setState({
      exportImageScale: newScaleValue,
      exportResolution: Math.round(squareCount * newScaleValue),
    });
  };

  handleResolutionChange = ({ currentTarget }) => {
    const { squareCount } = this.props;
    const newResolutionValue = Number(currentTarget.value);
    const minResolution = squareCount;
    const maxResolution = minResolution * this.MAX_SCALE_TIMES;

    let resolutionValue;
    if (newResolutionValue > maxResolution) {
      resolutionValue = maxResolution;
    } else if (newResolutionValue < minResolution) {
      resolutionValue = minResolution;
    } else {
      resolutionValue = newResolutionValue;
    }

    this.setState({
      exportResolution: newResolutionValue,
      exportImageScale: resolutionValue / squareCount,
    });
  };

  onClick = ({ target }) => {
    const exportHeader = target.closest('.export-options__header');
    if (exportHeader) {
      this.handleHeaderClick(exportHeader);
    }
  };

  render() {
    const { framesCount } = this.props;
    const {
      exportOption,
      activeExportOption,
      exportResolution,
      exportImageScale,
    } = this.state;

    let ActiveExportOptionComponent = () => '';
    let exportImageScaleHTML = '';
    if (exportImageScale < 10) {
      exportImageScaleHTML = `${exportImageScale.toPrecision(2)}x`;
    } else {
      exportImageScaleHTML = `${exportImageScale.toPrecision(3)}x`;
    }

    return (
      <div
        role="presentation"
        onClick={this.onClick}
        className="settings__main active-setting-block"
      >
        <h1 className="active-setting-block__header">Export</h1>
        <p className="active-setting-block__row">
          <span className="active-setting-block__text">Scale</span>
          <input
            ref={e => {
              this.scaleInput = e;
            }}
            onChange={this.handleScaleChange}
            value={exportImageScale}
            className="active-setting-block__input active-setting-block__range-input--setting-export"
            type="range"
            min="1"
            max={this.MAX_SCALE_TIMES}
            step="1"
          />
          <span
            style={{ width: '3rem', textAlign: 'center' }}
            className="active-setting-block__text active-setting-block__text--framed"
          >
            {exportImageScaleHTML}
          </span>
        </p>
        <p className="active-setting-block__row">
          <span className="active-setting-block__text">Resolution</span>
          <input
            onChange={this.handleResolutionChange}
            value={exportResolution}
            style={{ width: '4.9rem', marginLeft: '10px' }}
            className="active-setting-block__input active-setting-block__input--no-controls"
            type="number"
          />
          <span
            style={{ flex: 1, textAlign: 'center' }}
            className="active-setting-block__text"
          >
            x
          </span>
          <input
            onChange={this.handleResolutionChange}
            value={exportResolution}
            style={{ width: '4.9rem' }}
            className="active-setting-block__input active-setting-block__input--no-controls"
            type="number"
          />
        </p>
        <div className="active-setting-block__row export-options">
          <ul className="export-options__list">
            {exportOption.map(({ id, header, key, component }) => {
              if (activeExportOption === key) {
                ActiveExportOptionComponent = component;
              }
              return (
                <li
                  className={`export-options__header ${
                    activeExportOption === key
                      ? 'export-options__header--active'
                      : ''
                  }`}
                  data-key={key}
                  key={id}
                >
                  {header}
                </li>
              );
            })}
          </ul>
          <ActiveExportOptionComponent
            exportResolution={exportResolution}
            framesCount={framesCount}
          />
        </div>
      </div>
    );
  }
}

export default ExportSetting;
