import React, { Component } from 'react';
import PropTypes from 'prop-types';

class ImportSetting extends Component {
  static propTypes = {
    muteSettings: PropTypes.func.isRequired,
    localStorageKey: PropTypes.string.isRequired,
    renderMessageBox: PropTypes.func.isRequired,
    restoreProjectFromSave: PropTypes.func.isRequired,
  };

  hiddenFileInput = null;

  browseFilesButton = null;

  loadLocalSaveButton = null;

  handleFileUpload = () => {
    const {
      renderMessageBox,
      restoreProjectFromSave,
      muteSettings,
    } = this.props;

    const file = this.hiddenFileInput.files[0];
    if (!this.validateFile(file)) {
      return;
    }

    const fileReader = new FileReader();
    new Promise((resolve, reject) => {
      fileReader.readAsText(file, 'utf-8');
      fileReader.onload = () => {
        resolve(fileReader.result);
      };
      fileReader.onerror = reject;
    })
      .then(
        fileContents => {
          const fileData = JSON.parse(fileContents);
          restoreProjectFromSave(fileData);
        },
        () => {
          renderMessageBox(
            'File reader error happened, please check your file',
          );
        },
      )
      .catch(() => {
        renderMessageBox('Failed to restore project');
      })
      .finally(muteSettings);
  };

  validateFile = file => {
    if (!file) {
      return false;
    }
    if (!/\.piskel$/.test(file.name)) {
      return false;
    }
    return true;
  };

  handleLocalSaveUpload = () => {
    const {
      muteSettings,
      localStorageKey,
      renderMessageBox,
      restoreProjectFromSave,
    } = this.props;

    const localSave = localStorage.getItem(localStorageKey);
    if (!localSave) {
      renderMessageBox('Sorry we found no local save');
      return;
    }
    const localSaveData = JSON.parse(localSave);
    restoreProjectFromSave(localSaveData);
    muteSettings();
  };

  handleUploadFromFile = () => {
    this.hiddenFileInput.click();
  };

  onClick = ({ target }) => {
    if (target === this.loadLocalSaveButton) {
      this.handleLocalSaveUpload();
    } else if (target === this.browseFilesButton) {
      this.handleUploadFromFile();
    }
  };

  render() {
    return (
      <div
        role="presentation"
        onClick={this.onClick}
        className="settings__main active-setting-block"
      >
        <h1 className="active-setting-block__header">Load from Browser</h1>
        <p className="active-setting-block__row">
          <span className="active-setting-block__text">
            Load a
            <span className="active-setting-block__text--bold">
              {' local piskel '}
            </span>
            saved in this Browser
          </span>
        </p>
        <p className="active-setting-block__row">
          <button
            ref={e => {
              this.loadLocalSaveButton = e;
            }}
            type="button"
            className="active-setting-block__button"
          >
            Load local save
          </button>
        </p>
        <h1 className="active-setting-block__header">Load .piskel file</h1>
        <p className="active-setting-block__row">
          <span className="active-setting-block__text">
            Load a
            <span className="active-setting-block__text--bold">
              {' .piskel '}
            </span>
            file from your computer
          </span>
        </p>
        <p className="active-setting-block__row">
          <button
            ref={e => {
              this.browseFilesButton = e;
            }}
            type="button"
            className="active-setting-block__button"
          >
            Browser .piskel files
          </button>
          <input
            ref={e => {
              this.hiddenFileInput = e;
            }}
            onChange={this.handleFileUpload}
            type="file"
            accept=".piskel"
            hidden
          />
        </p>
      </div>
    );
  }
}

export default ImportSetting;
