import React from 'react';
import PropTypes from 'prop-types';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import './Tools.scss';

const Tools = ({ activeTool, tools, makeActive, children }) => {
  const onClick = ({ target }) => {
    const tool = target.closest('.tools__tool');
    if (!tool) {
      return;
    }
    const clickedName = tool.dataset.name;
    if (clickedName === activeTool) return;
    makeActive(clickedName);
  };

  const toolNames = Object.keys(tools);

  return (
    <section className="tools">
      <ul role="presentation" onClick={onClick} className="tools__container">
        {toolNames.map(name => {
          const { id, icon } = tools[name];
          const isActive = activeTool === name;
          const activeClass = isActive ? 'tools__tool--active' : '';
          return (
            <li
              key={id}
              data-name={name}
              className={`tools__tool ${activeClass}`}
            >
              <FontAwesomeIcon icon={icon} />
            </li>
          );
        })}
      </ul>
      {children}
    </section>
  );
};

Tools.propTypes = {
  activeTool: PropTypes.string.isRequired,
  makeActive: PropTypes.func.isRequired,
  tools: PropTypes.objectOf(
    PropTypes.shape({
      id: PropTypes.string.isRequired,
      icon: PropTypes.shape({}).isRequired,
    }),
  ).isRequired,
  children: PropTypes.element.isRequired,
};

export default Tools;
