import React, { PureComponent, Fragment } from 'react';
import './App.scss';
import genId from 'shortid';
import {
  faPen,
  faFill,
  faEraser,
  faEyeDropper,
} from '@fortawesome/free-solid-svg-icons';
import Header from '../Header/Header';
import Tools from '../Tools/Tools';
import ColorPick from '../ColorPick/ColorPick';
import Pen from '../Pen/Pen';
import Bucket from '../Bucket/Bucket';
import Eraser from '../Eraser/Eraser';
import EyeDrop from '../EyeDrop/EyeDrop';
import Frames from '../Frames/Frames';
import Palette from '../Palette/Palette';
import Preview from '../Preview/Preview';
import Settings from '../Settings/Settings';

class App extends PureComponent {
  state = {
    activeTool: 'pen',
    tools: {
      pen: {
        id: genId(),
        component: Pen,
        icon: faPen,
        paletteClassName: 'palette--tool-pen',
      },
      bucket: {
        id: genId(),
        component: Bucket,
        icon: faFill,
        paletteClassName: 'palette--tool-fill',
      },
      eraser: {
        id: genId(),
        component: Eraser,
        icon: faEraser,
        paletteClassName: 'palette--tool-eraser',
      },
      eyeDrop: {
        id: genId(),
        component: EyeDrop,
        icon: faEyeDropper,
        paletteClassName: 'palette--tool-eye-drop',
      },
    },
    imageSize: 0,
    activeFrameIndex: null,
    frameDrafts: [],
    framesCount: 0,
    squareCount: 32,
    imageURLs: ['data:,'],
    primaryColor: '#000000',
    secondaryColor: null,
    currentFPS: 12,
  };

  BACKUP_KEY = 'piskel-backup';

  frameContexts = [];

  updateActiveSquareCallback = null;

  componentDidMount() {
    if (!this.loadPreviousSession()) {
      this.setState({
        frameDrafts: [this.generateFrameDraft()],
        activeFrameIndex: 0,
      });
    }
    window.addEventListener('unload', this.backupCurrentProgress);
  }

  setNewSquareCount = newSquareCount => {
    const { framesCount, frameDrafts } = this.state;
    let i = 0;
    const newFrameDrafts = [];
    const newImageURLs = [];
    while (i < framesCount) {
      const oldDraftCtx = frameDrafts[i].ctx;
      const newDraft = this.generateFrameDraft();
      newDraft.ctx.canvas.height = newSquareCount;
      newDraft.ctx.canvas.width = newSquareCount;
      newDraft.ctx.drawImage(oldDraftCtx.canvas, 0, 0);
      newFrameDrafts[i] = newDraft;
      newImageURLs[i] = newDraft.ctx.canvas.toDataURL();
      i += 1;
    }
    this.setState({
      squareCount: newSquareCount,
      frameDrafts: newFrameDrafts,
      imageURLs: newImageURLs,
    });
  };

  updateImageSize = imageSize => {
    this.setState({ imageSize });
  };

  updateActiveSquare = square => {
    if (this.updateActiveSquareCallback) {
      this.updateActiveSquareCallback(square);
    }
  };

  generateFrameDraft = () => {
    const { squareCount } = this.state;
    const canvas = document.createElement('canvas');
    canvas.width = squareCount;
    canvas.height = squareCount;
    const ctx = canvas.getContext('2d');
    return {
      ctx,
      id: genId(),
    };
  };

  makeToolActive = name => {
    this.setState({ activeTool: name });
  };

  setNewFPS = newFPSval => {
    this.setState({
      currentFPS: newFPSval,
    });
  };

  addNewFrameDraft = () => {
    this.setState(({ frameDrafts, framesCount }) => {
      const newFrameDrafts = [...frameDrafts];
      const newFrameDraft = this.generateFrameDraft();
      newFrameDrafts.push(newFrameDraft);
      this.makeFrameActive(framesCount);
      return {
        frameDrafts: newFrameDrafts,
        framesCount: framesCount + 1,
      };
    });
  };

  makeFrameActive = index => {
    this.setState({
      activeFrameIndex: index,
    });
  };

  copyFrameDraft = index => {
    const { frameDrafts, framesCount, imageURLs } = this.state;
    const copyFromCtx = frameDrafts[index].ctx;

    const newFrameIndex = index + 1;
    const newFrameDraftData = this.generateFrameDraft();
    const newDataURL = imageURLs[index];
    this.fillAnyCanvas(newFrameDraftData.ctx, copyFromCtx.canvas);

    const newFrameDrafts = [...frameDrafts];
    const newImageURLs = [...imageURLs];

    newFrameDrafts.splice(newFrameIndex, 0, newFrameDraftData);
    newImageURLs.splice(newFrameIndex, 0, newDataURL);

    this.setState({
      frameDrafts: newFrameDrafts,
      framesCount: framesCount + 1,
      activeFrameIndex: newFrameIndex,
    });
  };

  deleteFrameDraft = index => {
    const { frameDrafts, framesCount, imageURLs } = this.state;
    let { activeFrameIndex } = this.state;

    const newFrameDrafts = [...frameDrafts];
    const newImageURLs = [...imageURLs];

    newFrameDrafts.splice(index, 1);
    this.frameContexts.splice(index, 1);
    newImageURLs.splice(index, 1);

    if (index === activeFrameIndex) {
      activeFrameIndex = index - 1;
      if (activeFrameIndex < 0) {
        activeFrameIndex = 0;
      }
    } else if (index < activeFrameIndex) {
      activeFrameIndex -= 1;
    }

    this.setState({
      framesCount: framesCount - 1,
      activeFrameIndex,
      frameDrafts: newFrameDrafts,
      imageURLs: newImageURLs,
    });
  };

  moveFrameDraft = (oldIndex, newIndex) => {
    const { frameDrafts, imageURLs } = this.state;
    const newFrameDrafts = [...frameDrafts];
    const newImageURLs = [...imageURLs];

    const frameDraftToMoveData = frameDrafts[oldIndex];
    const frameToMoveContext = this.frameContexts[oldIndex];
    const frameToMoveImage = newImageURLs[oldIndex];

    newFrameDrafts.splice(oldIndex, 1);
    newFrameDrafts.splice(newIndex, 0, frameDraftToMoveData);
    this.frameContexts.splice(oldIndex, 1);
    this.frameContexts.splice(newIndex, 0, frameToMoveContext);
    newImageURLs.splice(oldIndex, 1);
    newImageURLs.splice(newIndex, 0, frameToMoveImage);

    this.setState({
      frameDrafts: newFrameDrafts,
      activeFrameIndex: newIndex,
      imageURLs: newImageURLs,
    });
  };

  addToFrameContexts = (ctx, index) => {
    this.frameContexts[index] = ctx;
    this.updateFrameCanvas(index);
  };

  updateFrameCanvas = index => {
    const { frameDrafts, imageURLs } = this.state;
    const frameCtx = this.frameContexts[index];
    const frameDraft = frameDrafts[index].ctx;
    this.clearCanvas(frameCtx);
    this.fillAnyCanvas(frameCtx, frameDraft.canvas);

    const newImageURLs = [...imageURLs];
    newImageURLs[index] = frameDraft.canvas.toDataURL();
    this.setState({
      imageURLs: newImageURLs,
    });
  };

  updateActiveFrameCanvas = () => {
    const { activeFrameIndex } = this.state;
    this.updateFrameCanvas(activeFrameIndex);
  };

  setColors = (primaryColor, secondaryColor) => {
    let secondaryColorToSet;
    if (secondaryColor === undefined) {
      ({ secondaryColor: secondaryColorToSet } = this.state);
    } else {
      secondaryColorToSet = secondaryColor;
    }
    this.setState({
      primaryColor,
      secondaryColor: secondaryColorToSet,
    });
  };

  drawRectangleAt = (x, y, ctx, color = '#000000') => {
    ctx.fillStyle = color;
    ctx.fillRect(x, y, 1, 1);
  };

  clearRectangleAt = (x, y, ctx) => {
    ctx.clearRect(x, y, 1, 1);
  };

  clearCanvas = ctx => {
    ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);
  };

  fillAnyCanvas = (ctx, draftCanvas) => {
    const targetCtx = ctx;
    targetCtx.imageSmoothingEnabled = false;
    targetCtx.drawImage(
      draftCanvas,
      0,
      0,
      targetCtx.canvas.width,
      targetCtx.canvas.height,
    );
  };

  bresenhamsAlgorithm = (square1, square2, action, ctx, color = null) => {
    let x1 = square1.x;
    let y1 = square1.y;
    let x2 = square2.x;
    let y2 = square2.y;

    const rise = y2 - y1;
    const run = x2 - x1;

    if (!run) {
      if (y2 < y1) {
        [y1, y2] = [y2, y1];
      }
      for (let y = y1; y <= y2; y += 1) {
        action(x1, y, ctx, color);
      }
    } else {
      const m = rise / run;
      const adjust = m >= 0 ? 1 : -1;
      let offset = 0;
      let threshold = 0.5;
      if (m <= 1 && m >= -1) {
        const delta = Math.abs(m);
        let y = y1;
        if (x2 < x1) {
          [x1, x2] = [x2, x1];
          y = y2;
        }
        for (let x = x1; x <= x2; x += 1) {
          action(x, y, ctx, color);
          offset += delta;
          if (offset >= threshold) {
            y += adjust;
            threshold += 1;
          }
        }
      } else {
        const delta = Math.abs(run / rise);
        let x = x1;
        if (y2 < y1) {
          [y1, y2] = [y2, y1];
          x = x2;
        }
        for (let y = y1; y <= y2; y += 1) {
          action(x, y, ctx, color);
          offset += delta;
          if (offset >= threshold) {
            x += adjust;
            threshold += 1;
          }
        }
      }
    }
  };

  floodFillAlgorithm = (
    startX,
    startY,
    ctx,
    startColorData,
    fillColor,
    fillColorData,
    drawingBoundTop = 0,
  ) => {
    const pixelStack = [[startX, startY]];
    const canvasWidth = ctx.canvas.width;
    const canvasHeight = ctx.canvas.height;
    let x;
    let y;
    let pixelPos;
    let reachLeft;
    let reachRight;
    const imageData = ctx.getImageData(0, 0, canvasWidth, canvasHeight);

    while (pixelStack.length) {
      [x, y] = pixelStack.pop();
      pixelPos = (y * canvasWidth + x) * 4;
      while (
        y >= drawingBoundTop &&
        this.matchColor(pixelPos, imageData, startColorData)
      ) {
        pixelPos -= canvasWidth * 4;
        y -= 1;
      }
      pixelPos += canvasWidth * 4;
      y += 1;
      reachLeft = false;
      reachRight = false;
      while (
        y < canvasHeight &&
        this.matchColor(pixelPos, imageData, startColorData)
      ) {
        this.drawRectangleAt(x, y, ctx, fillColor);
        this.colorImageData(pixelPos, imageData, fillColorData);
        if (x > 0) {
          if (this.matchColor(pixelPos - 4, imageData, startColorData)) {
            if (!reachLeft) {
              pixelStack.push([x - 1, y]);
              reachLeft = true;
            }
          } else if (reachLeft) {
            reachLeft = false;
          }
        }

        if (x < canvasWidth - 1) {
          if (this.matchColor(pixelPos + 4, imageData, startColorData)) {
            if (!reachRight) {
              pixelStack.push([x + 1, y]);
              reachRight = true;
            }
          } else if (reachRight) {
            reachRight = false;
          }
        }
        y += 1;
        pixelPos += canvasWidth * 4;
      }
    }
  };

  matchColor = (pixelPos, imageData, startColorData) => {
    if (imageData.data[pixelPos + 3] === 0 && startColorData !== null) {
      return false;
    }
    if (!startColorData) {
      return imageData.data[pixelPos + 3] === 0;
    }
    const [colorR, colorG, colorB] = startColorData;
    const r = imageData.data[pixelPos];
    const g = imageData.data[pixelPos + 1];
    const b = imageData.data[pixelPos + 2];
    return r === colorR && g === colorG && b === colorB;
  };

  colorImageData = (pixelPos, imageData, colorData) => {
    const { data } = imageData;
    if (!colorData) {
      data[pixelPos] = 0;
      data[pixelPos + 1] = 0;
      data[pixelPos + 2] = 0;
      data[pixelPos + 3] = 0;
    } else {
      const [r, g, b] = colorData;
      data[pixelPos] = r;
      data[pixelPos + 1] = g;
      data[pixelPos + 2] = b;
      data[pixelPos + 3] = 255;
    }
  };

  eyeDrop = (x, y, ctx) => {
    const { data } = ctx.getImageData(x, y, 1, 1);
    if (data[3] === 0) {
      return null;
    }
    return data;
  };

  hexToData = hexColor => {
    const hexaValues = hexColor.slice(1);
    const red = parseInt(hexaValues.slice(0, 2), 16);
    const green = parseInt(hexaValues.slice(2, 4), 16);
    const blue = parseInt(hexaValues.slice(4), 16);
    return [red, green, blue];
  };

  colorDataToHex = ([r, g, b]) => {
    const rHex = r < 16 ? `0${r.toString(16)}` : r.toString(16);
    const gHex = g < 16 ? `0${g.toString(16)}` : g.toString(16);
    const bHex = b < 16 ? `0${b.toString(16)}` : b.toString(16);
    return `#${rHex}${gHex}${bHex}`;
  };

  getLightness = colorArray => {
    const colors = colorArray.map(rgbaVal => rgbaVal / 255);
    const max = Math.max(...colors);
    const min = Math.min(...colors);
    return ((max + min) / 2) * 100;
  };

  darknenColor = ([r, g, b, a], val = 40) => {
    const newR = Math.max(r - val, 0);
    const newG = Math.max(g - val, 0);
    const newB = Math.max(b - val, 0);
    return `rgba(${newR}, ${newG}, ${newB}, ${a})`;
  };

  lightenColor = ([r, g, b, a], val = 40) => {
    const newR = Math.min(r + val, 255);
    const newG = Math.min(g + val, 255);
    const newB = Math.min(b + val, 255);
    return `rgba(${newR}, ${newG}, ${newB}, ${a})`;
  };

  backupCurrentProgress = () => {
    const backupData = this.createSaveFileContents();
    localStorage.setItem(this.BACKUP_KEY, backupData);
  };

  loadPreviousSession = () => {
    const previousSession = localStorage.getItem(this.BACKUP_KEY);
    if (!previousSession) {
      return false;
    }
    const previousSessionData = JSON.parse(previousSession);
    this.restoreProjectFromSave(previousSessionData);
    return true;
  };

  createSaveFileContents = () => {
    const {
      imageURLs,
      activeFrameIndex,
      squareCount,
      primaryColor,
      secondaryColor,
      currentFPS,
    } = this.state;
    return JSON.stringify({
      imageURLs,
      activeFrameIndex,
      squareCount,
      primaryColor,
      secondaryColor,
      currentFPS,
    });
  };

  restoreProjectFromSave = ({
    imageURLs,
    activeFrameIndex,
    squareCount,
    primaryColor,
    secondaryColor,
    currentFPS,
  }) => {
    const newFrameDrafts = [];
    let i = 0;
    while (i < imageURLs.length) {
      newFrameDrafts[i] = this.generateFrameDraft();
      i += 1;
    }
    this.setState(
      {
        frameDrafts: newFrameDrafts,
        activeFrameIndex,
        imageURLs,
        framesCount: imageURLs.length,
        squareCount,
      },
      () => {
        const images = imageURLs.map(url => {
          return new Promise((res, rej) => {
            const img = new Image(squareCount, squareCount);
            img.src = url;
            img.onload = () => {
              res(img);
            };
            img.onerror = rej;
          });
        });
        Promise.all(images).then(imagesArray => {
          const { framesCount } = this.state;
          const frameDrafts = [];
          let index = 0;
          while (index < framesCount) {
            const image = imagesArray[index];
            const draft = this.generateFrameDraft();
            if (image) {
              this.fillAnyCanvas(draft.ctx, image);
              this.fillAnyCanvas(this.frameContexts[index], image);
            }
            frameDrafts[index] = draft;
            index += 1;
          }
          this.setState(
            {
              frameDrafts,
            },
            () => {
              this.setNewSquareCount(squareCount);
            },
          );
        });
      },
    );
    this.setColors(primaryColor, secondaryColor);
    this.setNewFPS(currentFPS);
  };

  render() {
    const {
      activeTool,
      tools,
      frameDrafts,
      squareCount,
      framesCount,
      activeFrameIndex,
      primaryColor,
      secondaryColor,
      imageSize,
      imageURLs,
      currentFPS,
    } = this.state;
    const activeDraftCtx =
      frameDrafts &&
      frameDrafts[activeFrameIndex] &&
      frameDrafts[activeFrameIndex].ctx;
    const canvasHelpers = {
      drawRectangleAt: this.drawRectangleAt,
      clearRectangleAt: this.clearRectangleAt,
      clearCanvas: this.clearCanvas,
      fillAnyCanvas: this.fillAnyCanvas,
      eyeDrop: this.eyeDrop,
      getLightness: this.getLightness,
      lightenColor: this.lightenColor,
      darknenColor: this.darknenColor,
      bresenhamsAlgorithm: this.bresenhamsAlgorithm,
      floodFillAlgorithm: this.floodFillAlgorithm,
      hexToData: this.hexToData,
      colorDataToHex: this.colorDataToHex,
    };
    const { component: ActiveTool, paletteClassName } = tools[activeTool];
    return (
      <Fragment>
        <Header />
        <main className="main">
          <Tools
            activeTool={activeTool}
            makeActive={this.makeToolActive}
            tools={tools}
          >
            <ColorPick
              primaryColor={primaryColor}
              secondaryColor={secondaryColor}
              setColors={this.setColors}
            />
          </Tools>
          <Frames
            moveFrameDraft={this.moveFrameDraft}
            addToFrameContexts={this.addToFrameContexts}
            activeFrameIndex={activeFrameIndex}
            frameDrafts={frameDrafts}
            framesCount={framesCount}
            addNewFrameDraft={this.addNewFrameDraft}
            makeFrameActive={this.makeFrameActive}
            copyFrameDraft={this.copyFrameDraft}
            deleteFrameDraft={this.deleteFrameDraft}
          />
          <Palette
            imageSize={imageSize}
            updateImageSize={this.updateImageSize}
            setActiveSquare={this.updateActiveSquare}
            squareCount={squareCount}
            updateActiveFrameCanvas={this.updateActiveFrameCanvas}
            activeDraftCtx={activeDraftCtx}
            activeTool={ActiveTool}
            paletteClassName={paletteClassName}
            canvasHelpers={canvasHelpers}
            primaryColor={primaryColor}
            setColors={this.setColors}
          />
          <Preview
            onMount={callback => {
              this.updateActiveSquareCallback = callback;
            }}
            imageSize={imageSize}
            squareCount={squareCount}
            framesCount={framesCount}
            activeFrameIndex={activeFrameIndex}
            imageURLs={imageURLs}
            currentFPS={currentFPS}
            setNewFPS={this.setNewFPS}
          />
          <Settings
            setNewSquareCount={this.setNewSquareCount}
            squareCount={squareCount}
            framesCount={framesCount}
            createSaveFileContents={this.createSaveFileContents}
            restoreProjectFromSave={this.restoreProjectFromSave}
          />
        </main>
      </Fragment>
    );
  }
}

export default App;
