import React, { Component } from 'react';
import PropTypes from 'prop-types';

class ResizeSetting extends Component {
  static propTypes = {
    squareCount: PropTypes.number.isRequired,
    muteSettings: PropTypes.func.isRequired,
    setNewSquareCount: PropTypes.func.isRequired,
  };

  state = {
    futureSquareCount: null,
  };

  MAX_GRID_SIZE = 128;

  widthInput = null;

  heightInput = null;

  static getDerivedStateFromProps({ squareCount }, { futureSquareCount }) {
    if (futureSquareCount === null) {
      return {
        futureSquareCount: squareCount,
      };
    }
    return {};
  }

  handleInputChange = ({ currentTarget }) => {
    if (
      currentTarget !== this.widthInput &&
      currentTarget !== this.heightInput
    ) {
      return;
    }
    const futureSquareCount = Number(currentTarget.value);
    if (Number.isNaN(futureSquareCount)) {
      return;
    }
    if (futureSquareCount > this.MAX_GRID_SIZE) {
      return;
    }
    this.setState({
      futureSquareCount,
    });
  };

  handleResize = () => {
    const { futureSquareCount } = this.state;
    const { muteSettings, squareCount, setNewSquareCount } = this.props;
    if (futureSquareCount <= 0) {
      return;
    }
    muteSettings();
    if (futureSquareCount === squareCount) {
      return;
    }
    setNewSquareCount(futureSquareCount);
  };

  render() {
    const { futureSquareCount } = this.state;
    return (
      <div
        role="presentation"
        onClick={this.onClick}
        className="settings__main active-setting-block"
      >
        <h1 className="active-setting-block__header">Resize</h1>
        <p className="active-setting-block__row active-setting-block__row--setting-reize">
          <span className="active-setting-block__text active-setting-block__text--setting-reize">
            Width
          </span>
          <input
            ref={e => {
              this.widthInput = e;
            }}
            onChange={this.handleInputChange}
            className="active-setting-block__input active-setting-block__input--no-controls"
            style={{
              width: 'width: 170px',
              marginRight: '0.4rem',
            }}
            type="number"
            min="1"
            max="128"
            value={futureSquareCount}
          />
          <span className="active-setting-block__text">px</span>
        </p>
        <p className="active-setting-block__row active-setting-block__row--setting-reize">
          <span className="active-setting-block__text active-setting-block__text--setting-reize">
            Height
          </span>
          <input
            ref={e => {
              this.heightInput = e;
            }}
            onChange={this.handleInputChange}
            className="active-setting-block__input active-setting-block__input--no-controls"
            style={{
              width: 'width: 170px',
              marginRight: '0.4rem',
            }}
            type="number"
            min="1"
            max="128"
            value={futureSquareCount}
          />
          <span className="active-setting-block__text">px</span>
        </p>
        <p className="active-setting-block__row">
          <button
            onClick={this.handleResize}
            type="button"
            className="active-setting-block__button"
          >
            Resize
          </button>
        </p>
      </div>
    );
  }
}

export default ResizeSetting;
