import React from 'react';
import PropTypes from 'prop-types';
import Frame from '../Frame/Frame';

const DragFrameBox = ({
  dragIndex,
  activeFrameIndex,
  topOffset,
  leftOffset,
  framesCount,
  addToFrameContexts,
}) => {
  const isActive = activeFrameIndex === dragIndex;
  return (
    <div
      className="frames__drag-frame-box"
      style={{
        top: `${topOffset}px`,
        left: `${leftOffset}px`,
      }}
    >
      <Frame
        index={dragIndex}
        isActive={isActive}
        framesCount={framesCount}
        addToFrameContexts={addToFrameContexts}
      />
    </div>
  );
};

DragFrameBox.propTypes = {
  addToFrameContexts: PropTypes.func.isRequired,
  dragIndex: PropTypes.number.isRequired,
  activeFrameIndex: PropTypes.number.isRequired,
  topOffset: PropTypes.number.isRequired,
  leftOffset: PropTypes.number.isRequired,
  framesCount: PropTypes.number.isRequired,
};

export default DragFrameBox;
